* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
  font-family: Montserrat, Arial, sans-serif;
}

html {
  background: #e2e2e2;
  font-size: 14px;
}

body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

h1,
h2,
h3,
label {
  font-family: Montserrat, Arial, sans-serif;
  font-size: 12px;
  font-weight: bold;
  color: #1f2041;
  text-transform: uppercase;
}

input + label {
  font-weight: normal;
  text-transform: none;
}

h1,
legend {
  font-size: 24px;
  line-height: 30px;
  font-family: "Open Sans", Arial, sans-serif;
  font-weight: bold;
  color: #1f2041;
}

h2 {
  font-size: 19px;
  font-family: "Open Sans", Arial, sans-serif;
}

input,
textarea,
select,
button {
  font: inherit;
  outline: none;
}

form {
  background: white;
  padding: 40px 30px 30px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

fieldset {
  border: 0 none;
}

.common__input_hide {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

.common__button_reset {
  border: 0;
  background: transparent;
}

ul {
  list-style: none;
}

body {
  background: #e5e5e5;
}

.header {
  background: #fff;
}

.header:nth-child(1) {
  margin-top: 150px;
  margin-bottom: 40px;
  z-index: 2;
}

.header:nth-child(2) {
  margin-bottom: 40px;
  z-index: 1;
}

body > footer {
  margin-bottom: 40px;
}

.footer + .footer {
  margin-bottom: 130px;
}

.menu {
  display: flex;
  align-items: center;
  position: relative;
  padding-left: 5px;
  height: 100%;
}

.menu__88320 {
  display: block;
}

.menu__icon {
  display: none;
  cursor: pointer;
}

.menu__list {
  list-style: none;
  background: white;
  display: flex;
}

.menu__link {
  font-family: Montserrat, Arial, sans-serif;
  color: rgba(31, 32, 65, 0.5);
  font-size: 14px;
  line-height: 17px;
  text-decoration: none;
  line-height: 36px;
}

.menu__link .menu__item:not(:last-of-type) {
  padding-right: 10px;
}

.menu__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.menu__item.menu__list_arrow {
  padding-right: 24px;
}

.menu > .menu__list > .menu__item.menu__item:not(:last-of-type) {
  margin-right: 30px;
}
.menu > .menu__list > .menu__item.menu__item:not(:last-of-type).menu__item_sub-menu {
  margin-right: 20px;
}

.menu__list:first-child > .menu__item:last-child > .menu__link {
  padding-right: 0px;
}

.menu__list_arrow {
  padding-right: 0px;
}

.menu__link + .menu__list {
  position: absolute;
  min-width: 150px;
  display: none;
  border-radius: 3px;
  box-shadow: 0 6px 16px 0 rgba(0, 0, 0, 0.07);
}

.menu__link + .menu__list > .menu__item {
  padding-left: 10px;
}

.menu__link + .menu__list .menu__list {
  position: absolute;
  top: -1px;
  right: -150px;
}

.menu__link + .menu__list .menu__item {
  position: relative;
}

.menu__list_arrow {
  position: relative;
}

.menu__list_arrow:after {
  content: "";
  width: 24px;
  font-family: "Material Icons";
  text-align: center;
  vertical-align: middle;
  font-size: 24px;
  line-height: 20px;
  bottom: 6px;
  right: 2px;
  color: rgba(31, 32, 65, 0.5);
}

.menu__link_bold {
  font-weight: bold;
  color: rgba(31, 32, 65, 0.75);
}

.menu__icon.menu_show + .menu__list {
  transition: transform 600ms;
  transform: translateX(0);
}

.button {
  border: none;
  font-family: Montserrat;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  background: none;
  color: #bc9cff;
  margin: 0px;
  padding: 10px 20px;
  cursor: pointer;
  text-transform: uppercase;
}

.button_simple {
  padding: 0;
}

.button_bordered {
  position: relative;
  background: transparent;
  border-radius: 22px;
  z-index: 1;
}

.button_bordered:before,
.button_bordered:after {
  content: " ";
  position: absolute;
  border-radius: 22px;
}

.button_bordered:before {
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -2;
  background: #bc9cff;
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
}

.button_bordered:after {
  top: 2px;
  bottom: 2px;
  left: 2px;
  right: 2px;
  background-color: white;
  z-index: -1;
}

.button_background_colored {
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
  border-radius: 22px;
  color: #ffffff;
}

.button_arrow {
  padding: 15px 20px;
  width: 100%;
  height: 44px;
  position: relative;
}

.button_arrow:after {
  font-family: "Material Icons";
  content: "arrow_forward";
  position: absolute;
  right: 10px;
  font-weight: normal;
  font-size: 24px;
  text-transform: none;
}

.button.button_simple.button_disabled {
  color: rgba(31, 32, 65, 0.5);
}

.button.button_bordered.button_disabled:before {
  background: linear-gradient(180deg, #bc9cff7f 0%, #8ba4f97f 100%);
}

.button.button_background_colored.button_disabled {
  background: linear-gradient(180deg, #bc9cff7f 0%, rgba(139, 164, 249, 0.5) 100%);
}

.button_dynamic-height {
  height: 100%;
  padding-top: 0;
  padding-bottom: 0;
}

.button_dynamic-width {
  width: 100%;
  padding-left: 0;
  padding-right: 0;
}

